import uniIcon from '@/components/uni-icon/uni-icon.vue';
import { post } from '@/api/user.js';
import { api } from '@/config/common.js';
import { cuLoading } from '@/components/custom/cu-loading.vue';
import tuiNomore from "@/components/nomore/nomore"
export default {
	data() {
		return {
			loading: false,
			loading2: false,
			stop: false,
			miao: 60,
			loginname: '',
			mobile: '',
			code: '',
			status: 0,
			password: '',
			new_password: '',
			re_new_password: '',
			title: '登录'
		};
	},
	components: { uniIcon ,tuiNomore},
	onLoad() {
	},
	onShow() {},
	methods: {
		handleNavbarClickLeft() {
			uni.switchTab({
				url: '/pages/my/my'
			});
		},
		handleReg(){
			uni.navigateTo({
				url: '/pages/my/login/reg'
			});
		},
		handleForget(){
			uni.navigateTo({
				url: '/pages/my/login/forget'
			});
		},
		getwxInfo() {
			var that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							that.ava_url = infoRes.userInfo.avatarUrl;
						}
					});
				}
			});
		},
		handleWxLogin(result) {
			const that = this 
			if(result.detail.userInfo){
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {	
					const code =loginRes.code
					const sendData = {'code':code,'logintype':1}
					// that.$refs.loading.open();
					that.$refs.loading.open()
					post(api.login, sendData)
						.then(res => {
							that.$refs.loading.close()
							if (res.status == 200 && res.data.returnCode == '0000') {
								let userInfo = {
									token: res.data.data.token,
									exp: res.data.data.exp,
									userId: res.data.data.userId
								};
								uni.setStorageSync('islogin', '1');	
								uni.setStorage({
									key: 'userInfo',
									data: userInfo,
									success: function() {
										uni.switchTab({
											url: '/pages/my/my'
										});
									}
								});
							} else {
								that.$api.msg(res.data.returnMessage);
							}
						})
						.catch(error => {
							that.$refs.loading.close()
							that.$api.msg('微信授权登录失败');
						});
					 }
					})
			}else{
				console.log("微信授权登录失败！");
			}
		},

		handleLogin() {
			const { loginname, password } = this;
			if (loginname.length == 0) {
				this.$api.msg('登录账号不能为空！');
				return;
			}
			if (password.length == 0) {
				this.$api.msg('登录密码不能为空！');
				return;
			}
			const sendData = {
				loginname,
				password,
				'logintype':0
			};
			this.loading = true;
			post(api.login, sendData)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						let userInfo = {
							token: res.data.data.token,
							exp: res.data.data.exp,
							userId: res.data.data.userId
						};
						uni.setStorageSync('islogin', '1');	
						uni.setStorage({
							key: 'userInfo',
							data: userInfo,
							success: function() {
								uni.switchTab({
									url: '/pages/my/my'
								});
							}
						});
					} else {
						this.$api.msg(res.data.returnMessage);
					}
					this.loading = false;
				})
				.catch(error => {
					this.loading = false;
					this.$api.msg('请求失败fail');
				});
		}
	}
};
